
import { Link } from 'react-router-dom';

import { ResumeSteps } from "../component/containers/ResumeSteps";

import dummyUserImg from '../assets/images/backgrounds/dummy-user.png'

//CSS
import '../assets/css/pages/create-resume.css'

function ProfessionalDetails() {
    return (
        <div className="main-content">
            <section className="personal-information">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Create Resume</h4>
                    </div>
                    <ResumeSteps completedSteps={2} />

                    <div className="create-resume-step-content light-blue-bg">
                        <div className="resume-header">
                            <Link to="/createResume/PersonalInformation" className='back-btn'><i className="bi bi-arrow-left"></i></Link>
                            <h4>Professional Details</h4>
                        </div>
                        <div className="resume-body">
                            <div className='create-resume-form'>
                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <label htmlFor="degree" className="form-label">Degree</label>
                                            <input type="text" className="form-control" id="degree" placeholder="Mention your highest qualification" />
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <label htmlFor="study_field" className="form-label">Field of Study</label>
                                            <input type="email" className="form-control" id="study_field" placeholder="Field of your study" />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <label htmlFor="college_university" className="form-label">College or University</label>
                                            <input type="text" className="form-control" id="college_university" placeholder="Mobile No." />
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className='row'>
                                            <div className='col-lg-6'>
                                                <div className="form-group">
                                                    <label htmlFor="job_start_year" className="form-label">From</label>
                                                    <input type="text" className="form-control" id="job_start_year" placeholder="Select start year" />
                                                </div>
                                            </div>
                                            <div className='col-lg-6'>
                                                <div className="form-group">
                                                    <label htmlFor="job_quit_year" className="form-label">To</label>
                                                    <input type="text" className="form-control" id="job_quit_year" placeholder="Select end year" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-xl-3 col-lg-4 col-sm-6'>
                                        <button className='button button-blue w-100'>Save</button>
                                    </div>
                                    <div className='col-xl-3 col-lg-4 col-sm-6'>
                                        <Link className='button button-blue w-100' to="/createResume/addSkills">Next</Link>
                                    </div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col-md-12'>
                                        <div className='added-qualification-list'>
                                            <h4 className='block-title'>My Qualification</h4>
                                            <ul>
                                                {
                                                    [...Array(3)].map((item, idx) => {
                                                        return (
                                                            <li className='qualification' key={idx}>
                                                                <span className='degree-name'>Bachlors in Marketing Field</span>
                                                                <div className='qualification-metas'>
                                                                    <div>2017-2020</div>
                                                                    <div>University of Baghdad</div>
                                                                </div>
                                                                <span className='field'>Field: Marketing</span>
                                                                <button className='text-btn'>Delete</button>
                                                            </li>
                                                        )
                                                    })
                                                }

                                            </ul>
                                        </div>
                                    </div>
                                </div>
                                <button className='text-btn save-exit-btn'>Save and Exit</button>
                            </div>
                        </div>
                    </div>

                </div>
            </section>
        </div>
    )
}

export default ProfessionalDetails;